:root {
  --darkB: #031927;
  --back: #cfe0c3;
  --title: #4696ae;
}

* {
  margin: 0;
  box-sizing: border-box;
  padding: 0;
  /* background-color: #fff; */
}

.page {
  display: flex;
  flex-direction: column;
}

main {
  position: relative;
  margin: 10px;
  min-height: 80vh;
}

main h1 {
  text-align: center;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-weight: bold;
  color: var(--darkB);
  font-size: 3rem;
}

.searchItems {
  margin: auto;
  width: 50%;
  text-align: center;
}

.searchItems input {
  width: 100%;
  padding: 5px;
  border-radius: 10px;
  text-align: center;
}

.searchItems Button {
  margin: 10px 0;
  padding: 10px;
  width: 30%;
  border: none;
  color: white;
  font-weight: 600;
  background-color: var(--darkB);
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.5s ease-out;
}

.searchItems Button:hover {
  background-color: rgb(97, 170, 238);
}

.title h2 {
  text-align: center;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  color: var(--darkB);
}

.title .underline {
  margin: 5px auto;
  width: 20%;
  border-bottom: 10px solid var(--title);
  /* height: 1px; */
}

.container {
  padding: 10px;
}

.item-container {
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-gap: 2rem;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
}

.single-tour {
  width: 100%;
  /* padding: 10px; */
  border-radius: 5px;
  transition: all 0.5s ease-out;
}

.single-tour:hover {
  box-shadow: 5px 5px 8px #888888;
}

.single-tour img {
  width: 100%;
  object-fit: cover;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

.single-tour footer {
  width: 100%;
  padding: 10px;
}

.single-tour footer h4 {
  padding: 2px;
  font-weight: bold;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  margin: 5px;
}

.single-tour footer .rating {
  color: rgb(191, 142, 19);
}

.single-tour footer p {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  padding: 5px;
  font-weight: 400;
  color: #4d4b4b;
}

.single-tour footer .delete-btn {
  margin: 10px 0;
  text-align: center;
  padding: 5px;
  width: 50%;
  transform: translateX(50%);
  border: 1px solid rgb(216, 115, 115);
  color: rgb(208, 109, 109);
  font-family: Helvetica, sans-serif;
  font-weight: 600;
  font-size: 15px;
  cursor: pointer;
  transition: all 1s ease-out;
}

.single-tour footer .delete-btn:hover {
  color: white;
  background-color: rgb(216, 115, 115);
}

.read-btn {
  border: none;
  color: var(--title);
  cursor: pointer;
  font-weight: 600;
}

.err {
  width: 50%;
  transform: translateX(50%);
  background-color: rgb(197, 113, 113);
  color: white;
  font-weight: 900;
  font-family: Helvetica, sans;
  padding: 5px;
}

.gif {
  width: 50px;
  height: 50px;
}

.buy {
  margin: 5px;
  text-decoration: none;
  display: block;
  text-align: center;
  padding: 5px;
  transform: translateX(45%);
  background-color: rgb(214, 81, 81);
  width: 50%;
  color: #fff;
  border-radius: 3px;
}

.base {
  /* color: white; */
  background-color: var(--darkB);
  align-items: center;
  color: #fff;
}

.base .container {
  color: #fff;
  display: flex;
  gap: 20px;
  justify-content: space-around;
  align-items: center;
}

.base a {
  display: block;
  padding: 5px;
  font-size: 20px;
  text-decoration: none;
  color: #fff;
  transition: all 0.5s ease-in-out;
}

.base a:hover {
  color: var(--title);
}

.base .copy {
  text-align: center;
}

@media screen and (max-width: 600px) {
  .base .container {
    flex-direction: column;
  }
}

.topAnime {
  display: flex;
  flex-direction: column;
  margin: 10px;
  gap: 0.5em;
  justify-content: center;
  align-items: center;
}

.topAnime .top {
  text-align: center;
}

.topAnime input {
  width: 3em;
}

.topAnime label {
  color: var(--title);
  font-weight: bold;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  font-size: 20px;
}

.topAnime button {
  font-family: cursive;
  font-weight: bold;
  width: 20%;
  text-align: center;
  padding: 10px;
  border: none;
  color: #fff;
  background-color: var(--darkB);
  border-radius: 5px;
  cursor: pointer;
}

.worldTop {
  margin: 2em;
  text-decoration: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.worldTop h2 {
  font-weight: 600;
  font-size: 2em;
  margin-bottom: 10px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  border-bottom: 5px solid var(--title);
}

.worldTop ul {
  list-style-type: none;
}

.worldTop li {
  margin: 2px 0;
  padding: 2px;
  font-size: 20px;
  color: #504f4f;
}

.topScore {
  font-weight: bold;
  color: rgb(87, 64, 7);
}
